@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Butcherman|Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cinzel+Decorative:900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Roboto', sans-serif;
font-size:1.2em;
background-color: #ff9a91;
color:#222;
}
i {background-color:blue;
    color:#fff;
    padding:5px;
    font-style:italic;}
    img {
    max-width:400px;
    max-height:300px;
    padding-left:auto;
    padding-right:auto;
    }
    .logoimgdiv {
        height: 50px;
        width: 120px;
        background-image: url(/static/media/Logo.48e68711.png);
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: contain;
    }
    .leader_container {
        width:100%;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .leaderboard {
        height: 90px;
        width: 728px;
        background-image: url(data:image/gif;base64,);
        background-repeat: no-repeat;
        background-position:center;
        background-size: contain;
    }
    h1 {
        font-family: Cinzel Decorative,cursive;
        margin-top: 0;
    }
    .heading {
        text-align:center;
        padding-top:25px;
    }
section.news {background-color: rgba(255,255,255,0.88);
    border-radius:8%;
    padding:2%;
    display:flex;
    flex-direction:column;
    align-items:center;
}


div.selectnews {
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items: center;
}

div.news_select_boxes {
    display:flex;
}

li {display:flex;
  align-items:center;
}
.one, .two {padding:20px;}

a {color:#555;}
.doc-footer {
  display:flex;
  flex-direction:column;
  justify-content: center;
  align-items:center;
}
.doc-footer a {
    color:#222;
    text-decoration:underline;
    -webkit-text-decoration-color:#222;
            text-decoration-color:#222;
}
@media all and (max-width: 1000px) {
    .leaderboard {
        height: 84px;
        width: 679px;
           }  
}
@media all and (max-width: 800px) {
    .leaderboard {
        height: 68px;
        width: 546px;
           }  
}

@media all and (max-width: 675px) {
  body {font-size:1rem;
max-width:90vw;
padding: 0 25px;
line-height:1.2rem;}
h2 {
    line-height:1.2em;
}
.one, .two {padding:5px 0px;}
  li {display:flex;
  flex-direction:column;
justify-items:center;
padding-right:20px;}
  img {max-width:85vw;}
.blank {
    height:250px;
}
.leaderboard {
    height: 45px;
    width: 364px;
    background-image: url(data:image/gif;base64,);
    background-repeat: no-repeat;
    background-position:center;
    background-size: contain;
}
}
@media all and (max-width: 575px) { 

    body {
        max-width: 560px;
        padding: 0 5px;
        line-height:1.2rem;
    }
    ul {
        max-width:99%;
        
    }
    .leaderboard {
        height: 41px;
        width: 328px;
           }  
}

@media all and (max-width: 400px) { 

    body {
        font-size:0.8rem;
        max-width: 360px;
        padding: 0 5px;
        line-height:1.15rem;
    }
    ul {
        max-width:99%;
        
    }
    .leaderboard {
        height: 34px;
        width: 275px;
           }  
}
@media all and (max-width: 325px) { 

    body {
        font-size:0.75rem;
        max-width: 300px;
        padding: 0 5px;
        line-height:1.05rem;
    }
   
    ul {
        max-width:99%;
        
    }
    .vms-div {
        min-height:350px;
    }
    
}
